import { useQuery } from '@tanstack/react-query';
import { supabase } from '../supabaseClient';
import { useState, useEffect } from 'react';

// Hook para verificar se o usurio logado  um administrador.
// Ele busca o usurio atual e verifica sua existncia na tabela 'admins'.
export const useAdmin = () => {
  const [user, setUser] = useState(null);

  useEffect(() => {
    const getUser = async () => {
      const { data } = await supabase.auth.getUser();
      setUser(data.user);
    };
    getUser();
  }, []);

  const { data: isAdmin, isLoading, error } = useQuery({
    queryKey: ['adminCheck', user?.id],
    queryFn: async () => {
      if (!user) return false;

      const { data, error } = await supabase
        .from('admins')
        .select('user_id')
        .eq('user_id', user.id)
        .single();

      if (error && error.code !== 'PGRST116') { // PGRST116 = row not found
        throw new Error(error.message);
      }

      return !!data;
    },
    enabled: !!user, // A query s  executada se houver um usurio.
  });

  return { isAdmin: isAdmin ?? false, isLoading, error };
};